.App {
  text-align: center;
}

body {
  background-color: #283618;
}

.bins {
  margin: 0px auto;
  width: 540px;
  background-color: grey;
  border-radius: 20%;
}

.bin {
  width: 130px;
  height: 130px;
  background-color: #606C38;
  border-radius: 50%;
  border: 20px solid black;
  float: left;
  margin: 5px;
  cursor: pointer;
}

.trash {
  width: 120px;
  height: 120px;
  -webkit-animation: trash-spin infinite 25s linear;
          animation: trash-spin infinite 25s linear;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 25s linear;
          animation: App-logo-spin infinite 25s linear;
  height: 80px;
}

.overall-data {
  color: #FEFAE0;
}

.empty {
  width: 120px;
  height: 120px;
}

@-webkit-keyframes trash-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes trash-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



